import { Injectable } from '@angular/core'
import { CanActivate, Router, UrlTree, NavigationEnd } from '@angular/router'
import { filter } from 'rxjs/operators'

@Injectable({
  providedIn: 'root',
})
export class LaunchGuard implements CanActivate {
  private previousUrl: string = ''

  constructor(private router: Router) {
    this.router.events
      .pipe(filter((event) => event instanceof NavigationEnd))
      .subscribe((event: any) => {
        this.previousUrl = event.url
      })
  }

  async canActivate(): Promise<boolean | UrlTree> {
    const url = new URL(window.location.href)
    const hasStripeSetupParams =
      url.searchParams.has('setup_intent') &&
      url.searchParams.has('setup_intent_client_secret') &&
      url.searchParams.has('redirect_status')

    if (
      window.location.pathname.includes('/launch') ||
      window.location.pathname.includes('/signup-minified') ||
      window.location.pathname.includes('/account/privacy') ||
      window.location.pathname.includes('/account/settings/cgu') ||
      window.location.pathname.includes('/renting') ||
      hasStripeSetupParams
    ) {
      return true
    }

    if (!this.previousUrl.includes('/launch')) {
      const currentUrl = window.location.pathname
      return this.router.createUrlTree(['/launch'], {
        queryParams: { next: currentUrl },
      })
    }

    return true
  }
}
