import { Component, OnInit, NgZone, effect, HostListener } from '@angular/core'
import { IonApp, IonRouterOutlet } from '@ionic/angular/standalone'
import { LanguageService } from './services/utils/language.service'
import { NavigationEnd, Router } from '@angular/router'
import { App, URLOpenListenerEvent } from '@capacitor/app'
import { defineCustomElements } from '@ionic/pwa-elements/loader'
import {
  CookiesService,
  CoookiesState,
} from './services/internal/cookies.service'
import { ModalController, AlertController } from '@ionic/angular/standalone'
import { CookiesComponent } from './components/modals/cookies/cookies.component'
import { Capacitor } from '@capacitor/core'
import { AppstateService } from './services/internal/appstate.service'
import { SentryService } from './services/external/sentry.service'
import { URLService } from './services/utils/url.service'
import { filter } from 'rxjs'
import { ScreenOrientation } from '@capacitor/screen-orientation'
import { Stripe } from '@capacitor-community/stripe'
import { NewsStateService } from './services/api-wrappers/news.service'
import { environment } from 'src/environments/environment'
import { PersonnalisationService } from './services/utils/personnalisation.service'
import { KYCWrapperService } from './services/api-wrappers/kyc.service'
@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  imports: [IonApp, IonRouterOutlet],
})
export class AppComponent implements OnInit {
  @HostListener('window:unload', ['$event'])
  unloadHandler(event: any) {
    this.appstateService.saveAppState()
    this.newsStateService.saveMandatoryState()
  }
  @HostListener('window:beforeunload', ['$event'])
  beforeUnloadHandler(event: any) {
    this.appstateService.saveAppState()
    this.newsStateService.saveMandatoryState()
  }

  constructor(
    private language: LanguageService,
    private router: Router,
    private zone: NgZone,
    private cookiesService: CookiesService,
    private modalController: ModalController,
    private alertController: AlertController,
    private appstateService: AppstateService,
    private sentryService: SentryService,
    private urlService: URLService,
    private newsStateService: NewsStateService,
    private personalisationService: PersonnalisationService,
    private kycService: KYCWrapperService
  ) {
    Stripe.initialize({
      publishableKey: environment.stripePublicKey,
    })
    defineCustomElements(window)
    this.initializeApp()
    effect(() => {
      this.onCookiesStateChange(this.cookiesService.cookiesState())
    })
  }

  async ngOnInit(): Promise<void> {
    await this.cookiesService.getPreferences()
    if (Capacitor.isNativePlatform()) {
      await ScreenOrientation.lock({ orientation: 'portrait' })
      if (this.cookiesService.cookiesState().state === 'not_already_set') {
        const preferences = {
          necessary: true,
          analytics: true,
        }
        await this.cookiesService.storePreferences(preferences)
      }
    }

    // CapacitorShake.addListener('shake', () => {
    //   this.sentryService.openFeedbackModal()
    // })

    this.router.events
      .pipe(filter((event) => event instanceof NavigationEnd))
      .subscribe((event: NavigationEnd) => {
        this.urlService.previousUrl.set(this.urlService.currentUrl())
        this.urlService.currentUrl.set(event.url)
      })
  }

  initializeApp() {
    this.language.initializeLanguage()
    this.personalisationService.setTitle()
    this.personalisationService.setColors()
    this.kycService.setKycDocumentTypes()
    App.addListener('appUrlOpen', (event: URLOpenListenerEvent) => {
      this.zone.run(async () => {
        let path = ''

        // Case 1: Custom URL scheme (com.ecovelo.xxxx://path)
        if (event.url.startsWith('com.ecovelo.')) {
          const parts = event.url.split('://')
          if (parts.length > 1) {
            path = parts[1]
          }
        }
        // Case 2: Universal Links (https://domain/path)
        else if (event.url.startsWith('https://')) {
          try {
            const url = new URL(event.url)
            // Combine pathname, search params, and hash
            path = url.pathname + url.search + url.hash
            // Remove leading slash if present for Angular router
            if (path.startsWith('/')) {
              path = path.substring(1)
            }
          } catch (error) {
            console.error('Failed to parse URL:', event.url, error)
          }
        }

        if (path) {
          this.router.navigateByUrl(path)
        }
      })
    })
  }

  private async onCookiesStateChange(state: CoookiesState) {
    switch (state.state) {
      case 'already_set':
        if (state.preferences.analytics) {
          this.sentryService.initSentry()
        }
        break
      case 'not_already_set':
        if (!Capacitor.isNativePlatform()) {
          const modal = await this.modalController.create({
            component: CookiesComponent,
            cssClass: 'ecl-cookies-modal',
            id: 'ecl-cookies-modal',
            backdropDismiss: false,
          })
          await modal.present()
        }
        break
      default:
        break
    }
  }
}
